<template>
  <van-action-sheet
    v-model="show"
    cancel-text="取消"
    class="share-box"
    @cancel="onCancel"
  >
    <div class="share-content" style="border-top: 1px solid transparent;">
      <van-cell-group>
        <van-field
          v-model="link"
          center
          clearable
          disabled
        >
          <div slot="button" class="share-but"  @click="copyNumber">复制链接</div>
        </van-field>
      </van-cell-group>
    </div>
  </van-action-sheet>
</template>

<script>
  import {handleClipboard} from '../../utils/clipboard'
    export default {
      name: "share",
      data() {
        return {
          show: true,
          link:'https://www.baidu.com/'
        };
      },
      methods:{
        copyNumber () { // 物流单号复制
          handleClipboard(this.link, event, () => {
            alert('链接已经复制在剪贴版')
          }, () => {
            alert('链接复制失败！')
          })
        },
        onCancel(){
          this.$router.push({
            name:'my'
          })
        }
      }
    }
</script>

<style scoped lang="scss">
  @import "../../style/my";
</style>



